import React from 'react';

import Link from 'next/link'
import { Col, Row } from "antd";

export default class FooterElement extends React.Component {
    convert = (string) => string.split(" ").join("-").toLowerCase();
    render(){
        let lists = this.props.children.map((item, i) => {
        return (
            <Link 
            key={i} 
            href={this.convert(this.props.page)+'/'+this.convert(item)}>
                {item}
            </Link>
        )})
        return(
            <div>
                <Link 
                href={'/'+this.convert(this.props.page)} 
                style={{color:'white', lineHeight:'1em'}}>
                    {this.props.page}
                </Link>
                {lists}
            </div>
        )
    }
}


